<template>
  <Card class="card">
     <Breadcrumb>
        <BreadcrumbItem>船舶中心</BreadcrumbItem>
        <BreadcrumbItem>船舶列表</BreadcrumbItem>
    </Breadcrumb>
      <div class='box-container'>
         <div style="margin:20px 0 40px 0">
              <Input placeholder="请输入船名/mmsi" style='width:220px' v-model="name" @keyup.enter.native="search()" /> <Button type="primary" @click="search()">搜索</Button>
        </div>
         <Table border :columns="columns1" :data="data1"></Table>
        <Page :total="total" class="pages" @on-change="changePage"
         show-elevator show-sizer  @on-page-size-change='sizeChange'
         :current="currentPage" :page-size="size" />
      </div>
  </Card>
</template>
<script>
    export default {
        data () {
            return {
                name:'',
                columns1: [
                     {
                        title: '#',
                        key: 'Index',
                         align:'center',
                         width:70
                    },
                    {
                        title: '船名',
                        key: 'shipName',
                        align:'center',
                        width:200
                    },
                    {
                        title: '船东',
                        key: 'name',
                        align:'center',
                          width:170
                    },
                    {
                        title: 'mmsi',
                        key: 'mmsi',
                        align:'center',
                         width:170
                    },
                    {
                        title: '船长',
                        key: 'shipLong',
                        align:'center',
                    },
                    {
                        title: '船宽',
                        key: 'shipWidth',
                        align:'center',
                    },
                    {
                        title: '载重吨位',
                        key: 'zTons',
                        align:'center',
                    },
                    {
                        title: '证书吨位',
                        key: 'bookTons',
                        align:'center',
                    },
                    {
                        title: 'TPC',
                        key: 'TPC',
                        align:'center',
                    },
                    {
                        title: '详情',
                        key: 'detail',
                         align:'center',
                         render: (h, params) => {
                           return h('Button', {
                             props:{
                               type:"primary"
                             },
                              style:{
                                display:'inline-block',
                                width:100,
                                background:'red;'
                              },
                              on:{
                                click:()=>{
                                    // console.log(123,params)
                                    let routeUrl = this.$router.resolve({
                                      path: "/shipCenter/shipdetail",
                                      query: {code:params.row.code}
                                });
                                window.open(routeUrl .href, '_blank');

                                }
                              }
                           },'查看')
                       }
                    },
                ],
                data1: [ ],
                  currentPage:1,
                  size:10,
                  total:20,
            }
        },
        methods:{
        async  shipList(){
            let data = {
              currentPage:this.currentPage,
              size:this.size,
            }
               try{

                 let res = await this.$http.shipList(data)
                 if(0==res.data.status &&  res.data.result instanceof Array){
                     this.total = res.data.total
                     let result = res.data.result
                     this.data1 = []
                     this.commonFn(result)
                 }
                 else{
                   this.$Notice.error({
                     desc:'暂无数据'
                   })
                   this.data1 = []
                   this.total=0
                 }
              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }
          },
        // 公共函数
        commonFn(result){
              result.forEach((item,index) => {
                        this.data1.push({
                             Index:Number((this.currentPage-1)*this.size)+Number(index)+1,
                             shipName: item.shipnameCn ||  item.shipnameEn,
                             name:(item.contactName || '') + '/' + (item.contactPhone || ''),
                             mmsi:item.mmsi,
                             shipLong:item.length,
                             shipWidth:item.breadth,
                             zTons:item.loadTons,
                             bookTons:item.certTons,
                             TPC:item.tpc,
                             id:item.shipId,
                             code:item.code,
                             detail:'查看'
                        })
                     });
        },
        changePage(currentPage){
            this.currentPage = currentPage;
          if(localStorage.searchInfo){
                this.search(currentPage)
            }
            else{
              this.shipList()
            }
          },
      sizeChange(size){
              this.size = size
              if(!this.name){
                this.shipList()
              }
              else{
                this.search()
              }
          },
      // 查询
        async  search(currentPage){
          if(!this.name){
            this.$Notice.error({
              desc:"请输入船名"
            })
            return false
          }
          currentPage ? this.currentPage = currentPage : this.currentPage =1
             let data = {
                  currentPage:this.currentPage,
                  size:this.size,
                  trem:this.name,
             }
             try{
                  let res = await this.$http.selectShipInfo(data)
                    this.total = res.data.total
                    this.data1 = []
                   if(!res.data.status &&  res.data.result instanceof Array){
                      localStorage.setItem('searchInfo',this.name)
                       this.$Notice.success({
                          desc:'查询成功'
                        })
                      let result = res.data.result
                      this.commonFn(result)
                   }
                   else{
                     this.$Notice.error({
                        desc:'暂无数据'
                      })
                      return false
                   }
             }
             catch(err){
               localStorage.removeItem('searchInfo')
               this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)
             }


          },
        },
        created(){
          this.shipList()
        },
         watch:{
            name(){
              if(!this.name){
                 this.size =10
                 this.shipList()
                 localStorage.removeItem('searchInfo')
              }
            }
        },
      destroyed(){
           localStorage.removeItem('searchInfo')
        }
    }
</script>
<style lang="scss" scoped>
.box{
  margin-top: 70px;
}
/deep/  .ivu-page{
  position: relative;
}
</style>



